import React from "react";

export const PlanInformer = (props) => {
    return (
        <table className="table">
            <tbody>
            <tr>
                <td> </td>
                <td>{props.plan.regularPrice}</td>
            </tr>
            <tr>
                <td>   </td>
                <td>{props.plan.upperPrice}</td>
            </tr>
            <tr>
                <td> </td>
                <td>{props.plan.limit <= 9999999 ? props.plan.limit : ''}</td>
            </tr>
            </tbody>
        </table>
    )
};


